// src/components/BriefingIntro.tsx
import React from "react";
import styled from "styled-components";

interface BriefingIntroProps {
  onlyStack?: boolean;
}

const Container = styled.div`
  padding: calc(30 / 690 * 100%);
  border: 2px solid #aaa;
  background: #fefefe;
`;

const SectionTitle = styled.h3`
  margin-bottom: calc(50 / 654 * 100%);
  font-weight: bold;
`;

const Item = styled.p`
  margin: calc(32 / 589 * 100%) 0;
  display: flex;
  gap: 4px;

  &:before {
    content: " ";
  }
`;

const Highlight = styled.span`
  background-color: #eaffc5;
  padding: 4px 8px;
  font-weight: bold;
  display: inline-block;
`;

const BriefingIntro: React.FC<BriefingIntroProps> = ({ onlyStack }) => {
  return (
    <Container className="font-22">
      <SectionTitle className="font-32">
          :  
      </SectionTitle>
      <Item>   .</Item>
      <Item>    (  UI,UX) .</Item>

      <Item>
        {onlyStack ? (
          <Highlight>
            React + Styled Components + TypeScript + Framer Motion .
          </Highlight>
        ) : (
          "React + Styled Components + TypeScript + Framer Motion ."
        )}
      </Item>
      <Item>,   .</Item>
      <Item> UI,UX  .</Item>
      <Item>     .</Item>
      <Item>SPA    .</Item>
    </Container>
  );
};

export default BriefingIntro;
