import React from "react";
import styled from "styled-components";

const Wrapper = styled.div`
  background: #fdfaf5;
  border: 2px solid #444;
  border-radius: 12px;
  padding: calc(40 / 690 * 100%) calc(20 / 690 * 100%) calc(60 / 690 * 100%);
  color: #222;
`;

const Step = styled.div`
  position: relative;
  padding: calc(20 / 690 * 100%) calc(20 / 690 * 100%);
  margin-bottom: calc(60 / 690 * 100%);
  background: #fff;
  border-radius: 6px;
  box-shadow: 2px 2px 0 #aaa, -1px 0px 3px #666;
`;

const Arrow = styled.div`
  margin: calc(-10 / 690 * 100vw) 0 calc(16 / 690 * 100vw)
    calc(20 / 690 * 100vw);
  color: #4a90e2;
`;

const FlowChunkSteps: React.FC = () => {
  return (
    <Wrapper className="font-20">
      <Step>
             <strong> </strong>.
      </Step>
      <Arrow className="font-32"></Arrow>
      <Step>
         <strong> id</strong>      
        .
      </Step>
      <Arrow className="font-32"></Arrow>
      <Step>
             <strong>id </strong>.
      </Step>
      <Arrow className="font-32"></Arrow>
      <Step>
         <strong>   </strong> .
      </Step>
    </Wrapper>
  );
};

export default FlowChunkSteps;
