import React from "react";
import styled from "styled-components";

const Container = styled.div`
  background-color: #fffde7;
  border: 2px solid #fdd835;
  border-radius: 8px;
  padding: calc(30 / 654 * 100%);
  margin: 0 auto;
  color: #333;
`;

const ParagraphGroup = styled.div`
  margin-bottom: calc(40 / 585 * 100%);
`;

const Title = styled.h3`
  margin-bottom: calc(10 / 585 * 100%);
  color: #5d4037;
  font-weight: 600;
`;

const Paragraph = styled.p`
  margin-bottom: calc(20 / 585 * 100%);
`;

const BoxLabel = styled.div`
  margin-top: calc(30 / 585 * 100%);
  padding: calc(20 / 585 * 100%);
  background-color: #f0f0f0;
  border-radius: calc(10 / 585 * 100%);
  font-weight: bold;
  text-align: center;

  span {
    animation: bounce 1.4s infinite;
    display: inline-block;
    margin-right: calc(20 / 690 * 100%);
  }

  @keyframes bounce {
    0%,
    100% {
      transform: translateY(0) rotate(0);
    }
    50% {
      transform: translateY(6px) rotate(0);
    }
  }
`;

const ClickGuide: React.FC = () => {
  return (
    <Container className="font-20">
      <ParagraphGroup>
        <Title className="font-24">   </Title>
        <Paragraph>
              ,    
               .
        </Paragraph>
      </ParagraphGroup>

      <ParagraphGroup>
        <Title className="font-24">     </Title>
        <Paragraph>
                   ,
                   
          .
        </Paragraph>
      </ParagraphGroup>

      <ParagraphGroup>
        <Title className="font-24">     </Title>
        <Paragraph>
               ,  
                   .
        </Paragraph>
      </ParagraphGroup>

      <BoxLabel>
        <span></span>      
      </BoxLabel>
    </Container>
  );
};

export default ClickGuide;
