import React from "react";
import styled, { keyframes } from "styled-components";

// width 33%  66%  33% 
const progressAnimation = keyframes`
  0% {
    width: 11%;
  }
  100% {
    width: 99%;
  }
`;

const Wrapper = styled.div`
  padding: calc(30 / 654 * 100%);
  max-width: 690px;
  margin: 0 auto;
  background-color: #fefae0;
  border: 2px solid #e0c68c;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.06);
`;

const Title = styled.h3`
  font-weight: 600;
  margin-bottom: calc(20 / 586 * 100%);
  color: #5d4037;
`;

const SubTitle = styled.p`
  display: flex;
  align-items: flex-start;
  gap: calc(8 / 586 * 100%);
  color: #888;
  margin-bottom: calc(20 / 586 * 100%);
`;

const DescriptionList = styled.ul`
  margin-bottom: calc(20 / 586 * 100%);
  color: #333;
  padding-left: calc(10 / 576 * 100%);
  list-style-type: disc;
`;

const ListItem = styled.li`
  margin-bottom: calc(10 / 576 * 100%);
  will-change: transform;
  display: flex;
  gap: 4px;

  &::before {
    content: "";
  }
`;

const BarWrapper = styled.div`
  width: 100%;
  height: 4px;
  background-color: #e0e0e0;
  border-radius: calc(6 / 586 * 100%);
  overflow: hidden;
  position: relative;
`;

const BarInner = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  background: linear-gradient(to right, #2b4d73, #4c6f91);
  animation: ${progressAnimation} 3s linear infinite;
  transform: translateZ(0);
`;

const ProgressBarDemo: React.FC = () => {
  return (
    <Wrapper className="font-20">
      <Title className="font-28">   </Title>
      <SubTitle className="font-22">
        <span role="img" aria-label="lightbulb">
          
        </span>
              
      </SubTitle>

      <DescriptionList>
        <ListItem>
                 .
        </ListItem>
        <ListItem>
                  
              .
        </ListItem>
        <ListItem>
                  
            ,       .
        </ListItem>
        <ListItem>
            UI      
            UX  .
        </ListItem>
      </DescriptionList>
      <BarWrapper>
        <BarInner />
      </BarWrapper>
    </Wrapper>
  );
};

export default ProgressBarDemo;
